"use client";

import { Bell, Link, LogOut, Settings, User } from "lucide-react";
import { Button } from "../ui/button";
import { useState } from "react";
import useSignalR from "@/hooks/use-signalR";
import { NotificationStatus } from "@/communication/responses/@types/Notification";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuShortcut,
  DropdownMenuTrigger,
} from "../ui/dropdown-menu";

export function NotificationComp() {
  const [messages, setMessages] = useState<NotificationStatus[]>([]);

  useSignalR("1", (msg) => {
    setMessages(msg);
  });

  console.log(messages);
  return (
    <DropdownMenu>
      <DropdownMenuTrigger asChild>
        <>
          <Bell className="h-5 w-5" />
          <span className="absolute -top-1 -right-1 flex h-4 w-4 items-center justify-center rounded-full bg-primary text-[10px] text-white">
            {messages.length}
          </span>
        </>
      </DropdownMenuTrigger>
      <DropdownMenuContent className="w-48" align="center">
        <DropdownMenuLabel>My Account</DropdownMenuLabel>
        <DropdownMenuSeparator />
        <Link href="/profile" className="w-full">
          <DropdownMenuItem>
            <User />
            <span>Profile</span>
            <DropdownMenuShortcut>⇧⌘P</DropdownMenuShortcut>
          </DropdownMenuItem>
        </Link>
        <Link href="/settings" className="w-full">
          <DropdownMenuItem>
            <Settings />
            <span>Settings</span>
            <DropdownMenuShortcut>⇧⌘S</DropdownMenuShortcut>
          </DropdownMenuItem>
        </Link>
        <DropdownMenuSeparator />
        <DropdownMenuItem>
          <LogOut />
          <span>Log out</span>
          <DropdownMenuShortcut>⇧⌘Q</DropdownMenuShortcut>
        </DropdownMenuItem>
      </DropdownMenuContent>
    </DropdownMenu>
  );
}
